h1,
h2,
h3,
h4,
p {
  margin: 0px;
}

.list {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.link {
  text-decoration: none;
}

a {
  text-decoration: none;
}

input,
textarea {
  outline: transparent;
}

.address {
  font-style: normal;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
